
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box
}

html {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Dosis', sans-serif;
  line-height: 1.6;
  color: #666;
  background: #F6F6F6;
}

#root {
  max-width: 1200px;
  margin: 0 auto;
}

.header {
  text-align: center;
  padding: 1.5rem 2.5rem;
  background-image: -webkit-linear-gradient(330deg, #fbc2eb 0%, #a6c1ee 100%);
  background-image: -o-linear-gradient(330deg, #fbc2eb 0%, #a6c1ee 100%);
  background-image: linear-gradient(120deg, #fbc2eb 0%, #a6c1ee 100%);
  margin: 0 0 2rem 0;
  font-size: 1.5rem;
  color: white;
}

img {
  display: block;
  margin: 1rem auto;
  max-width: 100%;
}

p {
  padding: 0 2.5rem 2.5rem;
  margin: 0;
}

.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.card {
  margin: 1rem;
  background: white;
  -webkit-box-shadow: 2px 4px 25px rgba(0, 0, 0, .1);
          box-shadow: 2px 4px 25px rgba(0, 0, 0, .1);
  border-radius: 12px;
  overflow: hidden;
  -webkit-transition: all .2s linear;
  -o-transition: all .2s linear;
  transition: all .2s linear;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.card:hover {
  -webkit-box-shadow: 2px 8px 45px rgba(0, 0, 0, .15);
          box-shadow: 2px 8px 45px rgba(0, 0, 0, .15);
  -webkit-transform: translate3D(0, -2px, 0);
      -ms-transform: translate3D(0, -2px, 0);
          transform: translate3D(0, -2px, 0);
}

@media screen and (min-width: 600px) {
  .card {
    -ms-flex-positive: 1;
        flex-grow: 1;
    -ms-flex-negative: 1;
        flex-shrink: 1;
    -ms-flex-preferred-size: calc(50% - 2rem);
        flex-basis: calc(50% - 2rem);
  }
}

@media screen and (min-width: 900px) {
  .card {
    -ms-flex-positive: 1;
        flex-grow: 1;
    -ms-flex-negative: 1;
        flex-shrink: 1;
    -ms-flex-preferred-size: calc(33% - 2rem);
        flex-basis: calc(33% - 2rem);
  }
}

.card:nth-child(2n) .header {
  background-image: -webkit-linear-gradient(330deg, #84fab0 0%, #8fd3f4 100%);
  background-image: -o-linear-gradient(330deg, #84fab0 0%, #8fd3f4 100%);
  background-image: linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%);
}

.card:nth-child(4n) .header {
  background-image: -webkit-linear-gradient(330deg, #ff9a9e 0%, #fecfef 100%);
  background-image: -o-linear-gradient(330deg, #ff9a9e 0%, #fecfef 100%);
  background-image: linear-gradient(120deg, #ff9a9e 0%, #fecfef 100%);
}

.card:nth-child(5n) .header {
  background-image: -webkit-linear-gradient(330deg, #ffc3a0 0%, #ffafbd 100%);
  background-image: -o-linear-gradient(330deg, #ffc3a0 0%, #ffafbd 100%);
  background-image: linear-gradient(120deg, #ffc3a0 0%, #ffafbd 100%);
}


